.search-form {
  display: flex;
  justify-content: center;
  padding: 10px;
}

.btn-secondary {
  margin-left: 30px;
}

.gallery {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: auto;
  margin-left: auto;
  list-style: none;
}

.gallery-item {
  margin-top: 30px;
  margin-left: 30px;
  width: 360px;
}

// .photo-card {
//   height: 300px;
// }

.gallery-image {
  display: block;
  width: 100%;
  height: 270px;
}

.stats {
  display: flex;
  width: 100%;
}

.stats-item {
  display: flex;
  align-items: center;
  margin-left: 10px;
}

.material-icons {
  color: blue;
  margin-right: 10px;
}

.button {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.button.is-hidden {
  display: none;
}

.button > .spinner.is-hidden {
  display: none;
}
